import { Component, OnInit, Output, EventEmitter, ViewChild, ViewEncapsulation, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { Observable, BehaviorSubject } from 'rxjs';
import { filter, map, startWith, debounceTime, takeUntil } from 'rxjs/operators';
import { FormControl } from '@angular/forms';
import { Subscription, Subject } from 'rxjs';

import { MatDrawer } from '@angular/material/sidenav';

import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'enquiry-type',
  templateUrl: './enquiry-type.component.html',
  styleUrls: ['./enquiry-type.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class EnquiryTypeComponent implements OnInit {

  isExpanded:boolean = false;

  // Private
  private _unsubscribeAll: Subject<any>;

  isDataLoaded:boolean = true;

  prgramData:string = "";

  @ViewChild('matDrawer', {static: true})
  matDrawer: MatDrawer;

  drawerMode: 'over';

  _typesList:any;
  fullTypesList:any = [];

  searchData:FormControl;

  search:string = '';
  apiSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 5;

  allData: any;

  onRefreshed: BehaviorSubject<boolean | null>;
  @Output() newEnquiryTypeEvent = new EventEmitter<boolean>();
  @Output() editEnquiryTypeEvent = new EventEmitter<any>();

  constructor(
    private _changeDetectorRef: ChangeDetectorRef,
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private sharedService:SharedService
  ) {

    // Set the private defaults
    this._unsubscribeAll = new Subject();

    this.onRefreshed = new BehaviorSubject(null);

    this._typesList = new BehaviorSubject(null);
    this.searchData = new FormControl('');

   }

  get types$(): Observable<any>{
    return this._typesList.asObservable();
  }


  onBackdropClicked() {

  }

  addEnquiryType(event) {

    event.stopPropagation();
    this.newEnquiryTypeEvent.next(true)

  }

  editEnquiryType(event, id) {
    event.stopPropagation();
    this.editEnquiryTypeEvent.next(id)
  }

  ngOnInit(): void {

    // Load types
    let res = [
      {
        "id": 107,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "Dance",
        "description": "some dance related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      },
      {
        "id": 108,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "Yoga",
        "description": "some yoga related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      },
      {
        "id": 109,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "Karate",
        "description": "some karate related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      }

    ];

    this.fullTypesList = res;
    this.allData = res;
    this._typesList.next(res);

    this.types$.pipe( filter(res => res != null),
    ).subscribe((res:any) => {

      this.totalItems = res.length;
      this.ItemStartIndex = 0;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

    });

  }

  ngOnDestroy(): void
    {
        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
    }

}
