import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { EnquirySetupComponent } from './enquiry-setup.component';
import { AddEnquiryTypeComponent } from './enquiry-type/add-enquiry-type/add-enquiry-type.component';
import { EnquirySetupResolver } from './enquiry-setup.resolver';

const routes: Routes = [
  { path: '', component: EnquirySetupComponent, canActivate: [AuthGuard],
    children: [
      {
        path         : 'type/:type/:id',
        component    : AddEnquiryTypeComponent,
        resolve: {
          category: EnquirySetupResolver
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EnquirySetupRoutingModule { }
