import { Component, OnInit, ViewChild, ViewEncapsulation, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { Observable, BehaviorSubject } from 'rxjs';
import { filter, map, startWith, debounceTime, takeUntil } from 'rxjs/operators';
import { FormControl } from '@angular/forms';
import { Subscription, Subject } from 'rxjs';

import { MatDrawer } from '@angular/material/sidenav';

import { SharedService } from 'src/app/shared/services/shared.service';
@Component({
  selector: 'enquiry-mode',
  templateUrl: './enquiry-mode.component.html',
  styleUrls: ['./enquiry-mode.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class EnquiryModeComponent implements OnInit {

  constructor(
    private _changeDetectorRef: ChangeDetectorRef,
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private sharedService:SharedService
  ) {

    // Set the private defaults
    this._unsubscribeAll = new Subject();

    this._modesList = new BehaviorSubject(null);
    this.searchData = new FormControl('');

   }

  isExpanded:boolean = false;

  // Private
  private _unsubscribeAll: Subject<any>;

  isDataLoaded:boolean = true;

  prgramData:string = "";

  @ViewChild('matDrawer', {static: true})
  matDrawer: MatDrawer;

  drawerMode: 'over';

  _modesList:any;
  fullTypesList:any = [];

  searchData:FormControl;

  search:string = '';
  apiSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 5;

  allData: any;

  get modes$(): Observable<any>{
    return this._modesList.asObservable();
  }

  addEnquiryMode() {

  }

  ngOnInit(): void {

    // Load modes
    let res = [
      {
        "id": 107,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "SMS",
        "description": "some SMS related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      },
      {
        "id": 108,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "Phone Call",
        "description": "some Phone Call related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      }

    ];

    this.fullTypesList = res;
    this.allData = res;
    this._modesList.next(res);

    this.modes$.pipe( filter(res => res != null),
    ).subscribe((res:any) => {

      this.totalItems = res.length;
      this.ItemStartIndex = 0;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

    });

  }

  ngOnDestroy(): void
    {
        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
    }

}
