import { Component, Input, OnInit, ViewEncapsulation, ViewChild, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { Observable, BehaviorSubject } from 'rxjs';
import { filter, map, startWith, debounceTime, takeUntil } from 'rxjs/operators';
import { FormControl } from '@angular/forms';
import { Subscription, Subject } from 'rxjs';

import { EnquiryTypeComponent } from 'src/app/modules/admin/enquiries/enquiry-setup/enquiry-type/enquiry-type.component'

import { MatDrawer } from '@angular/material/sidenav';

import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-enquiry-setup',
  templateUrl: './enquiry-setup.component.html',
  styleUrls: ['./enquiry-setup.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class EnquirySetupComponent implements OnInit {


  isDataLoaded:boolean = true;

  @ViewChild('matDrawer', {static: true})
  matDrawer: MatDrawer;

  drawerMode: 'over';
  private _unsubscribeAll: Subject<any>;


  constructor(
    private _changeDetectorRef: ChangeDetectorRef,
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private sharedService:SharedService
  ) {

    this._unsubscribeAll = new Subject();

  }

  onBackdropClicked() {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    // Go to the parent route
    this._router.navigate(['/enquiries/setup'], {relativeTo: route});
    this.matDrawer.close();

    // Mark for check
    this._changeDetectorRef.markForCheck();

  }

  addEnquiryType(event) {

    if(event) {

      // Get the current activated route
      let route = this._activatedRoute;
      while ( route.firstChild )
      {
          route = route.firstChild;
      }

      var id = this.sharedService.guid();

      this.sharedService.setMatDrawerId(id);
      this._router.navigate(['type/new', id], {relativeTo: route});

      //Mark for check
      this._changeDetectorRef.markForCheck();

    }

  }

  editEnquiryType(id) {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    this.sharedService.setMatDrawerId(id);
    this._router.navigate(['type/edit', id], {relativeTo: route});

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  onGlSearchFilter(event) {

  }

  ngOnInit(): void {


  }

}
